span
    padding-left: 20px

@keyframes heart
    0%
        transform: scale(1) rotate(45deg)

    50%
        transform: scale(1.1) rotate(45deg)

    100%
        transform: scale(1) rotate(45deg)

.heart
    margin-left: 85px
    margin-top: 30px
    width: 50px
    height: 50px
    display: inline-block
    animation: heart 1s ease infinite
    transform: rotate(45deg)
    position: relative
.heart::before,
.heart::after
    content: ''
    display: block
    width: 50px
    height: 50px
    border-radius: 50%
    position: absolute

.heart::before
    top: -31px
    left: 0

.heart::after
    top: 0
    left: -31px
.slider
    position: absolute
    z-index: 1
    background-color: gray
    border-radius: 56px
    color: black
    width: 200px
    display: flex
    justify-content: center
    align-items: center
    padding: 0px 3px 3px 3px
    span
        background-color: white
        padding: 0
